import React from "react";
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const SignIn = (props) => {


    return (<>
        <Modal show={props.modalSign} onHide={props.handleSignOpen} className="Content">
            <Modal.Header closeButton>
                <Modal.Title>Sign-Up</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="container">
                    <label htmlFor="uname"><b>Username</b></label>
                    <input type="Uname" placeholder="Enter your email id"></input>
                    <label htmlFor="Mobile"><b>Mobile Number</b></label>
                    <input type="Mobile" placeholder="Enter your moblie Number"></input>
                    <label htmlFor="Password"><b>Password</b></label>
                    <input type="Password" placeholder="Enter your Password"></input>
                    <label htmlFor="Password"><b>Re-Password</b></label>
                    <input type="Re-Password" placeholder="Enter your Re-Password"></input>
                    <button type="submit">Login</button>
                    <label>
                        <input type="Checkbox"  name="remember" defaultChecked /> Remember me
                    </label>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={props.handleSignOpen}>
                    Cancel
                </Button>
            </Modal.Footer>
        </Modal>
    </>);
}

export default SignIn;