import React from "react";
import { NavLink } from "react-router-dom";

const MenuPageData = (props) => {
    return (
        <>
            <>
            <div className="card polycornerMenu" style={{width:'18rem'}}>                
                <div className="card-body">
                    <h5 className="card-title text-white">{ props.title}</h5>
                    <p class="card-text">{props.price}</p>
                   
                </div>
                <NavLink to="/" class="btn btn-success buttonAlign">Order Btn</NavLink>
            </div>
        </>
        </>
    )
};

export default MenuPageData;