import React from "react-dom";
import MenuPageData from "../DataFetch/MenuPageData";
import ItemMenu from "../DataFetch/ItemMenu.jsx";


const Menu = () => {
    return (
        <>
            <div className="container">
                <div className="ImageMenu"><p className="TextMenu">Menu</p></div>
            </div>

            <div className="container pt-5 containerColor">
                <div className="row">
                    <div className="col">
                        <div className="row">
                            <div className="col">
                                <div className="row pt-4 mx-5">
                                    {
                                        ItemMenu.map((val, index) => {
                                            return <MenuPageData key={index}
                                                title={val.title}
                                                price={val.price}
                                            />
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>            
        </>

    )
}
export default Menu;