import React from "react";
import { NavLink } from "react-router-dom";
import logo from "../Image/Logo1.png";
import LoginModal from "../Component/Login.jsx";
import Signup from "../Component/SignIn.jsx";




class NavBar extends React.Component {
    state = {
        modalOpen: false
    }

    handleLoginOpen = () => {
        this.setState((prevState) => {
            return {
                modalOpen: !prevState.modalOpen
            }
        })
    }
    state = {
        modalSign: false
    }

    handleSignOpen = () => {
        this.setState((prevState) => {
            return {
                modalSign: !prevState.modalSign
            }
        })
    }

    render() {
        return (
            <>
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <div className="container">
                        <NavLink className="navbar-brand" to="/"><img src={logo} alt="Logo" width="60" /></NavLink>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                    <NavLink activeClassName="activeMenu" exact className="nav-link" aria-current="page" to="/">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="activeMenu" className="nav-link" to="/Aboutus">About-us</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="activeMenu" className="nav-link" to="/Contactus">Contact-us</NavLink>
                                </li>

                            </ul>
                            <div className="nav-item">
                                <button className="btn btn-outline-success" type="Sign-up" onClick={this.handleSignOpen}> Sign-up</button>
                                <button className="btn btn-outline-success ms-1" type="Login" onClick={this.handleLoginOpen}>Login</button>
                            </div>
                        </div>
                    </div>
                </nav>
                <LoginModal
                    modalOpen={this.state.modalOpen}
                    handleLoginOpen={this.handleLoginOpen} />

                <Signup
                    modalSign={this.state.modalSign}
                    handleSignOpen={this.handleSignOpen} />


            </>
        )

    }
}
export default NavBar;