import React, { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';



const Login = (props) => {
   const [email, setEmail] = useState("");
   const [password, setPassword] = useState("");
   return (
      <>
         <Modal show={props.modalOpen} onHide={props.handleLoginOpen} className="Content">
            <Modal.Header closeButton>
               <Modal.Title>Login</Modal.Title>
            </Modal.Header>
            <Modal.Body>
               <div className="container">
                  <label htmlFor="uname"><b>Username</b></label>
                  <input type="text" placeholder="Enter Username" autoComplete="off" name="uname"
                     value={email}
                     onChange={(e) => setEmail(e.target.value)} />

                  <label htmlFor="psw"><b>Password</b></label>
                  <input type="password" placeholder="Enter Password" name="psw" autoComplete="off"
                     value={password}
                     onChange={(e) => setPassword(e.target.value)} />

                  <button type="submit">Login</button>
                  <label>
                     <input type="Checkbox" name="remember" defaultChecked /> Remember me
                  </label>
               </div>

            </Modal.Body>
            <Modal.Footer>
               <Button variant="danger" onClick={props.handleLoginOpen}>
                  Cancel
               </Button>
            </Modal.Footer>
         </Modal>
      </>
   );
}

export default Login;